import Vue from 'vue';
import Router from 'vue-router';

import HasSeachNavContent	from '../components/HasSeachNavContent.vue';
import HomeComponent 		from '../components/content/HomeComponent';
import TypesComponent		from '../components/content/TypesComponent';
import ProductListConent 	from '../components/ProductListConent';
import ProductDtailComponent		 	from '../components/ProductDtailComponent';

Vue.use(Router)

// 全局守卫注册于 当前项目的  路由对象中
const router = new Router({
	mode:"history",
	linkActiveClass:"active",
	linkExactActiveClass:"active-exact",
	routes: [
		{path:"/",redirect:"/product"},
	  	{path: '/product',component: HasSeachNavContent,children:[
			{path:"",redirect:"home"},
			{path:"home",component:HomeComponent},
			// beforeEnter 路由内守卫  ==  路由独享守卫
			// 除 afterEach 守卫以外，其他守卫 使用方式和 参数 一模一样
			{path:"types",component:TypesComponent,beforeEnter(to,from,next){
				console.log("路由内守卫");
				next();
			}},
			// {path:"aaaa",component:TypesComponent},
		]},

		{path:"/productList/:title/:fcid?",component:ProductListConent,props:true},
		{path:"/showDetail/:itemId",component:ProductDtailComponent,props:true},
		// {path:"*",redirect:"/product"}
	]
});

const isLoginPage = ["/product/types"]

// router.beforeEach((to,form,next)=>{
// 	console.log("beforeEach2");
// 	next();
// });

// 全局守卫的注册 ，实际上就是 对路由对象的 指定方法的 回调进行定义
//    守卫的默认功能，属于 请求拦截
router.beforeEach((to,from,next)=>{
	// to : 将要前往的路由地址  是一个路由对象
	// from : 来自于那个路由地址  是一个路由对象
	// next : 后续操作  后续执行函数
	// console.log("beforeEach1");
	// console.log(to,from);
	next(); // 直接调用 next()  表示直接放行
});
// beforeEach 用于请求拦截  == 登录权限校验
// 	beforeEach 是一个链式守卫 ==> 会按照定义的顺序依次执行
router.beforeEach((to,form,next)=>{
	// console.log("beforeEach2");
	let isLogin = true;  //模拟用户登录状态  true 表示用户登录了  false  表示用户没有登录
	// if(isLoginPage.indexOf(to.path)!=-1){
	// 	if(isLogin){
	// 		next();
	// 	}else{
	// 		// path 路由地址
	// 		// 修改 to 的 路径
	// 		next(path);
	// 	}
	// }else{
	// 		next();
	// }
	if(isLoginPage.indexOf(to.path)!=-1&&!isLogin){
		next("/login");
	}
	next();
});

export default router;



