import Vue from 'vue';
import Vuex from 'vuex';

import home from './modules/home';
import topSeach from './modules/topSeach';
import productList from './modules/productList';
import types from './modules/types'

Vue.use(Vuex);

export default new Vuex.Store({
    modules:{
        home,
        topSeach,
        productList,
        types
    }
})


