<template>
    <div>
        <top-search>
            <i class="iconfont icon-xiaoxizhongxin"></i>
        </top-search>
        
        <transition enter-active-class="animated fadeInRight" leave-active-class="animated fadeOutLeft">
            <keep-alive>
                <router-view></router-view>
            </keep-alive>
        </transition>
       
        <bottom-nav-bar></bottom-nav-bar>
    </div>
</template>

<script>
import BottomNavBar from './content/base/BottomNavBar';
import TopSearch from './content/base/TopSearch'
export default {
    components:{
        BottomNavBar,TopSearch
    }
}
</script>



