<template>
    <div class="bs-box">
        <swiper :options="swiperOption">
            <swiper-slide v-for="(item,i) in warpImgs" :key="'bs'+i">
                <img :src="item.image_800" alt="">
            </swiper-slide>
            <div class="swiper-pagination" slot="pagination"></div>
        </swiper>
    </div>

</template>

<script>
// require styles
import 'swiper/dist/css/swiper.css'
import { swiper, swiperSlide } from 'vue-awesome-swiper'

export default {
    data() {
        return {
            swiperOption: {
                autoplay: 2000,
                loop: true,
                pagination: ".swiper-pagination",
                paginationClickable: true
            }
        };
    },
    props:{
        warpImgs:{type:Array,default:[]}
    },
    components:{
        swiper, swiperSlide
    }
};
</script>

<style lang="less">
.bs{
    &-box{
        overflow: hidden;
        img {
            width: 100%;
        }
        .swiper-pagination-bullet {
            width: 16px;/*px*/
            height: 16px;/*px*/
        }
    }
}
</style>


