<template>
    <div class="ml-box">
        <div class="box-title">猜你喜欢</div>
        <div class="like-list">
            <product-detail class="like-item" v-for="(product,i) in likes" :key="i" :product="product" :title="true"></product-detail>
        </div>
    </div>
</template>

<script>
export default {
    props:{
        likes:{type:Array,default:[]}
    }
}
</script>

<style lang="less">
@import url('../../../lessVar.less');
.ml{
    &-box{
        margin-top: 20px;/*px*/
        >.box-title{
            font-size: 30px;/*px*/
            color: @33;
            width: 550px;/*px*/
            background: url("../../../assets/line.png") center center no-repeat;
            background-size: 550px auto;/*px*/
            margin: 0 auto;
            text-align: center;
        }
        >.like-list{
            display: flex;
            flex-flow: row wrap;
            justify-content: left;
            margin: 30px 0px;/*px*/
            padding-left: 4%;
            >.like-item{
                width: 50%;
                font-size: 0px;
                padding-right: 4%;
            }
        }
    }
}
</style>


