<template>
	<div id="app">
		<div class="main-content">
            <router-view/>
		</div>
	</div>
</template>

<script>
import { mapActions } from "vuex";
export default {
    methods: {
        ...mapActions(["initHistory","loadSeachWord"])
    },
    mounted() {
        // 数据预装载
        this.initHistory();
        this.loadSeachWord();
    }
};
</script>

<style lang="less">
@import url("./lessVar.less");
*:not(.swiper-wrapper) {
    padding: 0px;
    margin: 0px;
    overflow: hidden;
}
#app {
    font-family: "Avenir", Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: @2c;
    background-color: @33;
    position: absolute;
    top: 0px;
    bottom: 0px;
    left: 0px;
    right: 0px;
    .main-content {
        width: @viewWidth; /*px*/
        max-width: @maxViewWidth; /*px*/
        background-color: @white;
        height: 100%;
        margin: 0 auto;
        height: 100%;
    }
}
</style>



