<template>
    <div class="tm-box">
        <ul>
            <li v-for="(type,i) in types" :key="'tm'+i">
                <img :src="type.image" alt="">
                <span>{{ type.title }}</span>
            </li>
        </ul>
    </div>
</template>

<script>

export default {
    props:{
        types:{type:Array,default:[]}
    }
};
</script>

<style lang="less" scoped>
@import url('../../../lessVar.less');
.tm {
    &-box {
        margin-top: 20px;/*px*/
        ul {
            list-style: none;
            display: flex;
            flex-flow: row wrap;
            justify-content: center;
            li {
                flex-grow: 1;
                width: 20%;
                text-align: center;
                margin-bottom:20px;/*px*/
                img {
                    width: 70%;
                }
                span {
                    display: block;
                    padding: 0px 10px;/*px*/
                    font-size: 26px;/*px*/
                    color: @5a;
                }
            }
        }
    }
}
</style>


