import api from '../../server/api';

const state = {
    hotWord:[],
    keyWord:"",
    history:[]
}

const getters = {

}

const actions = {
    loadSeachWord({ commit }){
        api.loadSeachWord({
            platform:"H5",
            code:"hotWord,sketch"
        }).then(({ data:{hotWord,sketch} })=>{
            commit("setHotWord",hotWord.data);
            commit("setKeyWord",sketch.data.query);
        });
    },
    setKeyWordAndHistory({ commit,state },params){
        commit("setKeyWord",params);
        if(state.history.indexOf(params)==-1){
            commit("addHistory",params);
            localStorage.setItem("history",JSON.stringify(state.history));
        }
        
    },
    initHistory({ commit },clear=false){
        let data = [];
        if(clear){
            commit("setHistory",data);
            localStorage.removeItem("history");
            return;
        }
        data = localStorage.getItem("history") || "[]";
        commit("setHistory",JSON.parse(data));
    }
}

const mutations = {
    setHotWord(state,data){
        state.hotWord = data;
    },
    setKeyWord(state,data){
        // console.log(data);
        state.keyWord = data;
    },
    setHistory(state,data){
        state.history = data;
    },
    addHistory(state,data){
        state.history.push(data);
    }
}
export default {
    state,
    getters,
    actions,
    mutations,
}


