<template>
    <div class="pd-box">
        <div class="product-img">
            <img :src="product.img||''">
            <div class="seach" @click="showDetailComponent(product.tradeItemId)"></div>
        </div>
        <ul class="tips-prop" v-if="!title">
            <li v-for="(wk,i) in product.props" :key="i">{{ wk }}</li>
        </ul>
        <ul class="tips-title" v-if="title" >
            <li v-if="title">{{ product.title }}</li>
        </ul>
        <div class="price_star">
            <span class="price">￥{{ product.price }}</span>
            <span class="star">
                {{ product.cfav }}
                <i class="iconfont icon-shoucang"></i>
            </span>
        </div>
    </div>
</template>

<script>
export default {
    props: {
        product: { type: Object, default: {} },
        title: { type: Boolean, default: false }
    },
    methods:{
        showDetailComponent(itemId){
            sessionStorage.setItem(itemId,JSON.stringify(this.product));
            this.$router.push("/showDetail/"+itemId);
        }
    }
};
</script>

<style lang="less">
@import url("../../lessVar.less");
.pd {
    &-box {
        box-sizing: border-box;
        background-color: @white;
        border-radius: 10px;
        margin-bottom: 30px; /*px*/
        > .product-img {
            position: relative;
            height: 480px;
            > img {
                width: 100%;
                height: 100%;
                border-radius: 10px 10px 0px 0px;
            }
            > .seach {
                position: absolute;
                right: 10px;
                bottom: 15px;
                width: 58px;
                height: 58px;
                background: url("../../assets/seach.png") center center
                    no-repeat;
                background-size: 58px 58px;
                cursor: pointer;
            }
        }
        > .tips-prop{
            height: 120px; /*px*/
            > li{
                display: inline-block;
                background-color: #d8e4ec;
                color: #5a6f7a;
                font-size: 26px; /*px*/
                margin: 10px 10px 0px 0px;/*px*/
                padding: 6px 12px;/*px*/
                border-radius: 6px;
            }
        }
        > .tips-title {
            height: 74px; /*px*/
            position: relative;
            > li {
                display: inline-block;
                background-color: #d8e4ec;
                color: #5a6f7a;
                font-size: 26px; /*px*/
                line-height: 32px; /*px*/
                text-indent: 20px; /*px*/
                background-color: @white;
                margin-top: 10px; /*px*/
                &:after {
                    content: "...";
                    background-color: @white;
                    height: 32px; /*px*/
                    padding-left: 30px;
                    padding-right: 10px;
                    position: absolute;
                    bottom: 0px;
                    right: 0px;
                    font-size: 26px; /*px*/
                    font-weight: 900;
                    background: linear-gradient(
                        to right,
                        transparent,
                        #fff 40%
                    );
                }
            }
        }
        > .price_star {
            height: 30px;
            line-height: 30px;
            padding: 12px 0px;
            color: @99;
            position: relative;
            background-color: @white;
            border-radius: 10px;
            > .price {
                max-width: 45%;
                font-size: 30px; /*px*/
                font-weight: 700;
                color: @33;
                overflow: hidden;
            }
            > .star {
                font-size: 28px; /*px*/
                overflow: hidden;
                float: right;
                padding-right: 20px;
                i {
                    font-size: 28px; /*px*/
                }
            }
        }
    }
}
</style>


