<template>
    <div class="plc-box">
        <top-search>
            <i class="iconfont icon-xiangzuojiantou" @click="goBackPage()"></i>
        </top-search>
        <div class="product-list">
            <div class="sort">
                <ul>
                    <li v-for="(v,i) of productList.sortFilter" 
                        :class="v.sortKey==params.sort?'active':''" 
                        :key="'psf'+i" 
                        @click="sortProducts(v.sortKey)"
                    >
                        <span>{{ v.title }}</span>
                    </li>
                    <li>
                        <span>价格</span>
                    </li>
                </ul>
            </div>
            <scroll-view class="wrapper">
                <div class="content">
                    <product-detail class="product-item" v-for="(product,i) in productList.products" :key="i" :product="product" :title="false"></product-detail>
                </div>
            </scroll-view>
        </div>
    </div>
</template>

<script>
import TopSearch from "./content/base/TopSearch";
import { mapActions, mapState } from "vuex";

export default {
    data() {
        return {
            params: {
                page: 1,
                sort: "pop"
            },
            backPage:null
        };
    },
    props: {
        fcid: { type: String, default: null },
        title: { type: String, default: "" }
    },
    computed: {
        ...mapState(["productList"])
    },
    components: {
        TopSearch
    },
    methods: {
        ...mapActions(["loadProducts"]),
        goBackPage() {
            if(this.backPage){
                this.$router.push(this.backPage);
                return
            }
            this.$router.back();
        },
        sortProducts(data){
            this.params.sort = data;
            this.loadPageDatas();
        },
        loadPageDatas(data) {
            let params = this;
            let temp = {
                page: this.params.page,
                sort: this.params.sort
            };
            if(data){
                params = data;
            }
            if (params.fcid) {
                temp.fcid = params.fcid;
                temp.title = params.title;
                temp.cKey = 16;
            } else {
                temp.q = params.title;
                temp.cKey = 46;
            }
            this.loadProducts(temp);
        }
    },
    mounted() {
        this.loadPageDatas();
    },
    beforeRouteEnter (to, from, next) {
        next((vm)=>{
            vm.backPage = from.path;
        });
    },
    // 监视 路由 不变 参数 改变
    beforeRouteUpdate (to, from, next) {
        this.loadPageDatas(to.params);
        // console.log(to,from);
        next();
    }
};
</script>

<style lang="less">
@import url("../lessVar.less");
.plc {
    &-box {
        height: 100%;
        width: @viewWidth; /*px*/
        max-width: @maxViewWidth; /*px*/
        margin: @topHeight 0px 0px 0px; /*px*/
        > .product-list {
            @sortHeight: 88px;
            @sortFontSize: 28px;
            @sortPadding: (@sortHeight - @sortFontSize)/2;
            .sort {
                border-bottom: 1px solid @cc;
                ul {
                    list-style: none;
                    color: @33;
                    display: flex;
                    flex-flow: row nowrap;
                    li {
                        flex-grow: 1;
                        padding: @sortPadding 0px; /*px*/
                        line-height: @sortFontSize; /*px*/
                        display: inline-block;
                        text-align: center;
                        cursor: pointer;
                        &.active {
                            color: @active;
                        }
                        > span {
                            display: block;
                            height: @sortFontSize; /*px*/
                            line-height: @sortFontSize; /*px*/
                            font-size: @sortFontSize; /*px*/
                            border-right: 1px solid @33;
                        }
                        &:last-child > span {
                            border-right: none;
                        }
                    }
                }
            }
            .wrapper {
                position: absolute;
                left: 0px;
                right: 0px;
                top: @topHeight+ @sortHeight; /*px*/
                bottom: 0px;
                > .content {
                    display: flex;
                    flex-flow: row wrap;
                    justify-content: left;
                    margin: 30px 0px; /*px*/
                    padding-left: 4%;
                    > .product-item {
                        width: 50%;
                        font-size: 0px;
                        padding-right: 4%;
                    }
                }
            }
        }
    }
}
</style>



