<template>
    <div class="pd-c-box">
        <scroll-view class="wrapper">
            <div class="content">
                aaa
            </div>
        </scroll-view>
    </div>
</template>

<script>
export default {
    data() {
        return {
            product: {}
        };
    },
    props: {
        itemId: { type: String, default: "" }
    },
    methods: {
        parseProduct() {
            let str = sessionStorage.getItem(this.itemId);
            if (str) {
                this.product = JSON.parse(str);
            }
        }
    },
    mounted() {
        this.parseProduct();
    },
    beforeRouteLeave (to, from, next) {
        sessionStorage.removeItem(this.itemId);
        next();
    }
    
};
</script>

<style lang="less">
.pd-c {
    &-box {
    }
}
</style>


