import api from '../../server/api';

const state = {
    warpImgs: [],
    types: [],
    bebefit: [],
    hotTypes: [],
    likes: []
}

const getters = {

}

const actions = {
    loadHomeDatas({ commit }) {
        api.loadDatasByPids({
            pids: "51822,106930,51833,51836",
            appPlat: "m"
        }).then(({ data }) => {
            commit("setWapImgs", data[51822].list);
            commit("setTypes", data[106930].list);
            commit("setBebefit", data[51833].list);
            commit("setHotTypes", data[51836].list);
        })
    },
    loadLikes({ commit }) {
        api.loadDatasBySeachWord({
            pid: "9750",
            sort: "pop",
            cKey: 46
        }).then(({ result: { wall: { docs } } }) => {
            commit("setLikes", docs);
        });
    }
}

const mutations = {
    setWapImgs(state, data) {
        state.warpImgs = data;
    },
    setTypes(state, data) {
        state.types = data;
    },
    setBebefit(state, data) {
        state.bebefit = data;
    },
    setHotTypes(state, data) {
        state.hotTypes = data;
    },
    setLikes(state, data) {
        state.likes = data;
    }
}

export default {
    state,
    getters,
    actions,
    mutations,
}


