<template>
    <div class="ht-box">
        <div class="box-title">热门分类</div>
        <ul>
            <li v-for="(type,i) in hotTypes" :key="'ht'+i">
                <img :src="type.image">
                <span>{{ type.title }}</span>
            </li>
        </ul>
    </div>
</template>

<script>
export default {
    props:{
        hotTypes:{type:Array,default:[]}
    }
}
</script>

<style lang="less">
@import url('../../../lessVar.less');
.ht{
    &-box{
        margin-top: 30px;/*px*/
        >.box-title{
            font-size: 30px;/*px*/
            color: @33;
            width: 550px;/*px*/
            background: url("../../../assets/line.png") center center no-repeat;
            background-size: 550px auto;/*px*/
            margin: 0 auto;
            text-align: center;
        }
        >ul{
            margin-top: 30px;/*px*/
            list-style: none;
            display: flex;
            flex-flow:  row wrap;
            justify-content:center;
            li{
                flex-grow: 1;
                width: 25%;
                text-align: center;
                margin-bottom: 20px;/*px*/
                img{
                    width: 70%;
                }
                span{
                    display: block;
                    padding: 0px 10px;
                    font-size: 26px;/*px*/
                    color: @5a;
                }
            }
        }
    }
}
</style>


