<template>
    <div>
        <div class="ts-box" v-show="seachStart">
            <slot></slot>
            <div class="seach-input" @click="changeSeach">
                <i class="iconfont icon-sousuo"></i>
                <span>{{ topSeach.keyWord }}</span>
            </div>
            <i class="iconfont icon-gouwuche"></i>
        </div>

        <div class="ts-box" v-show="!seachStart">
            <i class="iconfont icon-xiangzuojiantou" @click="changeSeach"></i>
            <input class="user-input" type="text" :value="topSeach.keyWord" @change="setKeyWord($event.target.value)">
            <span class="seach-btn" @click="pageToProductSeach()">搜索</span>
        </div>

        <transition enter-active-class="animated bounceInUp" leave-active-class="animated bounceOutDown">
            <div class="ts-back" v-show="!seachStart">
                <div class="box">
                    <p>
                        <i class="iconfont icon-sousuo"></i>
                        <span>历史搜索</span>
                        <i class="iconfont icon-lajixiang clear-words" @click="initHistory(true)" ></i>
                    </p>
                    <ul class="word" @click="pageToProductSeach($event.target.innerHTML)">
                        <li class="no-style" v-if="topSeach.history.length==0">暂无历史搜索</li>
                        <li v-for="(word,i) in topSeach.history" :key="i">{{ word }}</li>
                    </ul>
                </div>
                <div class="box">
                    <p>
                        <i class="iconfont icon-shoucang"></i>
                        <span>热门搜索</span>
                    </p>
                    <ul class="word" @click="pageToProductSeach($event.target.innerHTML)">
                        <li v-for="(hw,i) in topSeach.hotWord" :key="'hw'+i" :style="{color:hw.color}">{{ hw.query }}</li>
                    </ul>
                </div>
            </div>
        </transition>
    </div>
</template>

<script>
import { mapActions,mapMutations, mapState } from "vuex";
export default {
    data() {
        return {
            seachStart: true,
            history: []
        };
    },
    computed: {
        ...mapState(["topSeach"])
    },
    methods: {
        ...mapActions(["setKeyWordAndHistory","initHistory"]),
        ...mapMutations(["setKeyWord"]),
        changeSeach() {
            this.seachStart = !this.seachStart;
        },
        pageToProductSeach(keyWord){
            if(!keyWord){
                keyWord = this.topSeach.keyWord;
            }
            this.setKeyWordAndHistory(keyWord);
            this.seachStart = true;
            // if(/^\/productList/.test(this.$route.path)){
            //     return;
            // }
            this.$router.push("/productList/"+keyWord);
        }
    }
};
</script>

<style lang="less">
@import url("../../../lessVar.less");
.ts {
    &-box {
        position: fixed;
        top: 0px;
        width: @viewWidth; /*px*/
        max-width: @maxViewWidth; /*px*/
        background-color: @white;
        border-bottom: @border;
        text-align: center;
        height: @topHeight; /*px*/
        display: flex;
        flex-flow: row nowrap;
        justify-content: center;
        line-height: @topHeight; /*px*/
        > .iconfont {
            font-size: @topHeight / 2; /*px*/
        }
        > i,
        .seach-btn {
            flex-grow: 1;
            cursor: pointer;
        }
        > .seach-btn:hover {
            color: @active;
        }
        > .seach-input {
            text-align: left;
            flex-grow: 8;
            background-color: @ea;
            margin: @topHeight / 5 0px; /*px*/
            border-radius: 10px;
            height: @topHeight / 5 * 3; /*px*/
            line-height: @topHeight / 5 * 3; /*px*/
            padding-left: @topHeight / 5; /*px*/
            color: #666666;
            cursor: pointer;
            > i {
                font-size: @topHeight / 5+6; /*px*/
            }
        }
        > .user-input {
            flex-grow: 5;
            margin: @topHeight / 5 0px; /*px*/
            border-radius: 10px;
            height: @topHeight / 5 * 3; /*px*/
            line-height: @topHeight / 5 * 3; /*px*/
            padding-left: @topHeight / 5; /*px*/
            outline: none;
            border: 1px solid #888;
            color: #333333;
            font-size: 30px; /*px*/
            &:focus {
                border: 1px solid #f9446b;
            }
        }
        > .seach-btn {
            font-size: 32px; /*px*/
        }
    }
    &-back {
        background-color: white;
        position: absolute;
        width: @viewWidth;
        top: @topHeight; /*px*/
        bottom: 0px; /*px*/
        z-index: 99;
        > .box {
            padding: 16px 40px;
            border-bottom: 1px solid #ccc;
            i {
                font-size: 30px; /*px*/
                font-weight: 500;
            }
            i.clear-words {
                float: right;
                cursor: pointer;
            }
            span {
                font-size: 30px; /*px*/
                color: @5a;
                padding-left: 16px;
                font-weight: 500;
            }
            > .word {
                color: @5a;
                padding: 14px 0px 14px 50px;
                > li {
                    font-size: 26px; /*px*/
                    display: inline-block;
                    padding-right: 10px;
                    border: 1px solid #cccccc;
                    text-align: center;
                    padding: 6px 12px;
                    border-radius: 6px;
                    margin-right: 20px;
                    margin-bottom: 6px;
                    cursor: pointer;
                }
                > li.no-style {
                    border: none;
                }
            }
        }
        > .box:last-child {
            border: none;
        }
    }
}
</style>




