<template>
    <div class="hc-box">
        <scroll-view class="wrapper">
            <div class="content">
                <banner-swiper :warpImgs="home.warpImgs"></banner-swiper>
                <type-menu :types="home.types"></type-menu>
                <super-benefit :bebefit="home.bebefit"></super-benefit>
                <hot-types :hotTypes="home.hotTypes"></hot-types>
                <may-like :likes="home.likes"></may-like>
            </div>
        </scroll-view>
    </div>
</template>

<script>
import { mapState,mapActions } from "vuex";
import BannerSwiper from './items/BannerSwiper';
import TypeMenu from './items/TypeMenu';
import SuperBenefit from './items/SuperBenefit';
import HotTypes from './items/HotTypes';
import MayLike from './items/MayLike';
export default {
    data(){
        return {
            num:40
        }
    },
    computed:{
        ...mapState(['home'])
    },
    methods:{
        ...mapActions(['loadHomeDatas','loadLikes'])
    },
    components:{
        BannerSwiper,TypeMenu,SuperBenefit,HotTypes,MayLike
    },
    mounted(){
        this.loadHomeDatas();
        this.loadLikes();
    }
};
</script>

<style lang="less">
@import url("../../lessVar.less");
.hc-box {
    position: absolute;
    top: 0px;
    bottom: 0px;
    width: @viewWidth;/*px*/
    max-width:@maxViewWidth;/*px*/
    margin: @topHeight 0px @bottomHeight 0px; /*px*/
    >.wrapper{
        height: 100%;
        width: 100%;
    }
}
</style>




