import jsonp from "../util/fetchJsonpUtil";

const mogucdn_jsonp = "https://mce.mogucdn.com/jsonp";

const mogujie_jsonp = "https://mce.mogujie.com/jsonp";

const list_mogujie = "https://list.mogujie.com";

const rate_mogujie = "https://rate.mogujie.com/h5/rate/jsonp/x6.rate.ratelist/v1";

export default {

    loadDatasBySeachWord(params){
        let url = `${list_mogujie}/search`;        
        return jsonp.request(url,params)
    },

    loadDatasByPids(params){
        let url = `${mogucdn_jsonp}/multiget/3`;
        return jsonp.request(url,params)
    },

    loadSeachWord(params){
        let url = `${list_mogujie}/module/mget`;
        return jsonp.request(url,params);
    },

    loadTypesLeft(params){
        let url = `${mogucdn_jsonp}/multiget/3`;
        return jsonp.request(url,params);
    },

    loadTypesRightMenu(params){
        // let url = `${mogujie_jsonp}/makeup/3`;
        let url = `${mogucdn_jsonp}/get/3`;
        return jsonp.request(url,params);
    },

    loadTypeRightDatas(params){
        let url = `${list_mogujie}/search`;
        return jsonp.request(url,params);
    }

}


