import api from '../../server/api';

const state = {
    leftMenu:[],
    rightMenu:[],
    sortFilter:[],
    products:[],
    fcid:"",
    sort:"pop",
    page:1
}

const getters = {

}

const actions = {
    loadTypesLeftMenu({ commit }){
        api.loadTypesLeft({
            appPlat:"m",
            pids:117330
        }).then(({ data })=>{
            commit("setLeftMenu",data[117330].list);
        })
    },
    loadRightMenu({commit},pid="3627"){
        api.loadTypesRightMenu({
            appPlat:"m",
            pid
        }).then(({ data })=>{
            commit("setRightMenu",data.list);
        })
    },
    loadRightProduct({commit},{fcid="10062603",page=1,sort="pop"}){
        commit("setFcid",fcid);
        commit("setSort",sort);
        commit("setPage",page);

        api.loadTypeRightDatas({
            fcid,
            page,
            sort,
            cKey:"h5-cube"
        }).then(({ result })=>{
            commit("setSortFilter",result.sortFilter);
            commit("setProducts",result.wall.docs);
        })
    }
}

const mutations = {
    setLeftMenu(state,data){
        state.leftMenu = data;
    },
    setRightMenu(state,data){
        state.rightMenu = data;
    },
    setSortFilter(state,data){
        state.sortFilter = data;
    },
    setProducts(state,data){
        state.products = data;
    },
    setFcid(state,data){
        state.fcid = data;
    },
    setSort(state,data){
        state.sort = data;
    },
    setPage(state,data){
        state.page = data;
    }
}

export default {
    state,
    getters,
    actions,
    mutations,
}


