import api from '../../server/api';

const state = {
    products:[],
    totle:0,
    sortFilter:[],
    priceFilter:[]
}

const getters = {

}

const actions = {
    loadProducts({ commit },params){
        api.loadDatasBySeachWord(params).then(({ result })=>{
            commit("setProducts",result.wall.docs);
            commit("setTotle",result.wall.total);
            commit("setSortFilter",result.sortFilter);
            commit("setPriceFilter",result.priceFilter);

        })
    }
}

const mutations = {
    setProducts(state,data){
        state.products = data;
    },
    setTotle(state,data){
        state.totle = data;
    },
    setSortFilter(state,data){
        state.sortFilter = data;
    },
    setPriceFilter(state,data){
        state.priceFilter = data;
    }
}

export default {
    state,
    getters,
    actions,
    mutations,
}


