<template>
    <div class="type-right-box">
        <scroll-view class="wrapper" :data="types.rightMenu">
            <div class="content">
                <ul class="menu">
                    <li v-for="(v,i) of types.rightMenu" 
                        :key="'rm'+i"
                        @click="changePage(v.link,v.title)"
                    >
                        <img :src="v.image">
                        <span>{{ v.title }}</span>
                    </li>
                </ul>
                <ul class="sort">
                    <li v-for="(v,i) of types.sortFilter" 
                        :class="v.sortKey==types.sort?'active':''" 
                        :key="'trs'+i"
                        @click="sortProducts(v.sortKey)"
                    >
                        <span>{{ v.title }}</span>
                    </li>
                </ul>
                <div class="products">
                    <product-detail class="product-item" 
                        v-for="(product,i) in types.products" 
                        :key="i" :product="product" 
                        :title="true">
                    </product-detail>
                </div>
            </div>
        </scroll-view>
    </div>
</template>

<script>
import { mapActions,mapState } from "vuex";
export default {
    computed:{
        ...mapState(["types"])
    }, 
    methods:{
        ...mapActions(["loadRightMenu","loadRightProduct"]),
        sortProducts(data){
            // this.sort = data;
            this.loadRightProduct({fcid:this.types.fcid,sort:data});
        },
        changePage(fcidUrl,title){
            let params = fcidUrl.split("?")[1]
                                .split("&")[0]
                                .replace("fcid=","");
            console.log(params);
            this.$router.push("/productList/"+title+"/"+params);
        }
    },
    mounted(){
        this.loadRightMenu();
        this.loadRightProduct({});
    }
}
</script>

<style lang="less">
.type-right{
    &-box{
        width: 100%;
        height: 100%;
        >.wrapper{
            height: 100%;
            width: 100%;
            > .content{
                > .menu{
                    list-style: none;
                    display: flex;
                    flex-flow: row wrap;
                    justify-content: left;
                    // margin: 30px 0px; /*px*/
                    padding-left: 4%;
                    li{
                        display: inline-block;
                        text-align: center;
                        width: 100%/3;
                        font-size: 0px;
                        box-sizing: border-box;
                        padding-right: 4%;
                        margin-top: 30px;
                        img{
                            width: 100%;
                        }
                        span{
                            display: block;
                            font-size: 26px;/*px*/
                            padding: 20px 0px;
                        }
                    }
                }
                > .sort{
                    list-style: none;
                    display: flex;
                    flex-flow: row wrap;
                    justify-content: space-between;
                    margin-top: 20px;
                    border-bottom: 1px solid #cccccc;
                    border-top: 1px solid #cccccc;
                    li{
                        flex-grow: 1;
                        text-align: center;
                        font-size: 30px;/*px*/
                        padding: 20px 0px;
                        span{
                            border-right: 1px solid #cccccc;
                            display: block;
                        }
                        &:last-child span{
                            border-right: none;
                        }
                        &.active{
                            color: #f9446b;
                        }
                    }
                }
                > .products{
                    display: flex;
                    flex-flow: row wrap;
                    justify-content: left;
                    margin: 30px 0px; /*px*/
                    padding-left: 4%;
                    > .product-item {
                        width: 50%;
                        font-size: 0px;
                        padding-right: 4%;
                    }
                }
            }
        }
    }
}
</style>


