<template>
    <div class="benefit-box">
        <div class="box-title">超实惠-促销直达</div>
        <div class="box-item-list">
            <div v-for="(item,i) in bebefit" :key="'benefit'+i" :class="'item'+ (i<3?i:'') ">
                <p class="item-title">{{item.title}}</p>
                <p class="item-viceTitle">{{item.viceTitle|deleteBraces}}</p>
                <img class="image" :src="item.image" alt="">
            </div>
        </div>
    </div>
</template>

<script>
export default {
    props: {
        bebefit:{type:Array,default:[]}
    },
    filters: {
        deleteBraces(word) {
            return word.replace(/{|}/g, "");
        }
    }
};
</script>

<style lang="less">
@import url('../../../lessVar.less');
.benefit {
    &-box {
        margin-top: 10px;/*px*/
        >.box-title {
            font-size: 30px;/*px*/
            color: @33;
            width: 550px;/*px*/
            background: url("../../../assets/line.png") center center no-repeat;
            background-size: 550px auto;/*px*/
            margin: 0 auto;
            text-align: center;
        }
        >.box-item-list {
            border-top: 1px solid #cccccc;
            border-bottom: 1px solid #cccccc;
            margin-top: 10px;/*px*/
            text-align: center;
            font-size: 0px;
            > div {
                float: left;
            }
            .item-title {
                font-size: 30px;/*px*/
                font-weight: 600;
                height: 32px;/*px*/
                line-height: 32px;/*px*/
            }
            .item-viceTitle {
                font-size: 24px;/*px*/
                color: @active;
            }
            .item {
                width: 100%/3;
                height: 240px;
                box-sizing: border-box;
                border-top: @border;
                border-right: @border;
                .image {
                    width: 150px;
                    height: 150px;
                }
                .item-title {
                    margin-top: 16px;
                }
            }
            .item:last-child {
                border-right: none;
            }
            .item0 {
                box-sizing: border-box;
                border-right: @border;
                width: 50%;
                height: 276px;
                .item-title {
                    margin-top: 30px;
                }
                .image {
                    width: 187px;
                    height: 187px;
                }
            }
            .item1 {
                border-bottom: @border;
            }
            .item1,
            .item2 {
                box-sizing: border-box;
                padding-left: 23px;
                text-align: left;
                width: 50%;
                height: 140px;
                position: relative;
                .item-title {
                    margin-top: 36px;
                }
                .image {
                    position: absolute;
                    top: 0px;
                    right: 0px;
                    height: 138px;
                    width: 138px;
                }
                .item-viceTitle {
                    text-align: left;
                    margin-bottom: 35px;
                }
            }
        }
    }
}
</style>



