<template>
    <div class="type-left-box">
        <scroll-view class="wrapper">
            <div class="content">
                <ul>
                    <li v-for="(v,i) of types.leftMenu" 
                        :key="'tlm'+i" 
                        :class="v.maitKey==showActive?'active':''" 
                        @click="changeRight(v.maitKey,v.miniWallkey)"
                    >
                        <span>{{ v.title }}</span>
                    </li>
                </ul>
            </div>
        </scroll-view>
    </div>
</template>

<script>
import { mapActions,mapState } from "vuex";
export default {
    data(){
        return {
            showActive:"3627"
        }
    },
    computed:{
        ...mapState(["types"])
    }, 
    methods:{
        ...mapActions(["loadTypesLeftMenu","loadRightMenu","loadRightProduct"]),
        changeRight(pid,fcid){
            this.showActive = pid;
            this.loadRightMenu(pid);
            this.loadRightProduct({fcid})
        }
    },
    mounted(){
        this.loadTypesLeftMenu();
    }
}
</script>

<style lang="less">
@import url("../../../lessVar.less");
.type-left{
    &-box{
        height: 100%;
        width: 100%;
        >.wrapper{
            height: 100%;
            width: 100%;
            ul{
                list-style: none;
                li{
                    padding: 30px 0px;
                    span{
                        font-size: 30px;/*px*/
                        display: block;
                        text-align: center;
                        border-left: 8px solid #f6f6f6;
                    }
                }
                li.active{
                    background-color: white;
                    span{
                         border-left-color: @active;
                    }
                }
            }
        }
    }
}
</style>


